import Vue from 'vue'
import Router from 'vue-router'
import { MainPage, PortfolioPage, EssayPage, BusinessCardPage, PedagogicalActivitiesWithChildrenPage, PedagogicalFindPage, MyPupilsPage, CounselingForParentsPage, MyAchievementsPage, TheSuccessesOfMyPupilsPage, MethodicalPiggyBankPage, GeneralizationPage } from '@/pages'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,

  routes: [
    {
      path: '/',
      name: 'main',
      component: MainPage
    },
    {
      path: '/essay',
      name: 'essay',
      component: EssayPage
    },
    {
      path: '/portfolio',
      name: 'portfolio',
      component: PortfolioPage
    },
    {
      path: '/businesscard',
      name: 'businesscard',
      component: BusinessCardPage
    },
    {
      path: '/pedagogicalactivitieswithchildren',
      name: 'pedagogicalactivitieswithchildren',
      component: PedagogicalActivitiesWithChildrenPage
    },
    {
      path: '/pedagogicalfind',
      name: 'pedagogicalfind',
      component: PedagogicalFindPage
    },
    {
      path: '/methodicalpiggybank',
      name: 'methodicalpiggybank',
      component: MethodicalPiggyBankPage
    },
    {
      path: '/mypupils',
      name: 'mypupils',
      component: MyPupilsPage
    },
    {
      path: '/counselingforparents',
      name: 'counselingforparents',
      component: CounselingForParentsPage
    },
    {
      path: '/myachievements',
      name: 'myachievements',
      component: MyAchievementsPage
    },
    {
      path: '/thesuccessesofmypupils',
      name: 'thesuccessesofmypupils',
      component: TheSuccessesOfMyPupilsPage
    },
    {
      path: '/generalization',
      name: 'generalization',
      component: GeneralizationPage
    }
  ]
})
